@use 'base/variables' as v;

.actions {
    margin-top: 3rem;
    display: flex;
    justify-content: space-between;

    &--center {
        @extend .actions;
        justify-content: center;
    }

    &__link {
        color: v.$black;
        font-size: 1.5rem;
        font-weight: v.$regular;
    }
}