@use 'base/variables' as v;
@use 'base/mixins' as m;

.paquetes {

    &__grid {
        @include m.container;
        @include m.grid(1, 3rem);
        @include m.tablet {
            @include m.grid(3, 3rem);
            align-items: flex-start;
        }
    }

    &__submit {
        @include m.button(v.$blue, v.$white);
        margin-top: 2rem
    }
}

.paquete {
    @include m.card;
    &:nth-child(3) {
        order: -1;

        @include m.tablet {
            order: unset;
        }
    }

    &__lista {
        @include m.reset-list;
        line-height: 1.5;
    }
    &__precio {
        font-size: 5.5rem;
        margin: 3rem 0;
        color: v.$blue;
        font-weight: v.$bold;
        text-align: center;
    }
}