@use 'base/variables' as v;
@use 'base/mixins' as m;

.bloques {
    @include m.container;
    @include m.tablet {
        margin-top: 4rem;
    }

    &__grid {
        @include m.grid(1, 3rem);
        @include m.desktop {
            @include m.grid(2, 3rem);
        }
    }
}

.bloque {
    padding: 2rem;
    color: v.$white;
    border-radius: 1rem;

    &:nth-child(1) {
        @include m.bg-gradient(#F97316);
    }
    &:nth-child(2) {
        @include m.bg-gradient(#A855F7);
    }
    &:nth-child(3) {
        @include m.bg-gradient(#65A30D);
    }
    &:nth-child(4) {
        @include m.bg-gradient(#17769E);
    }
    &__heading {
        margin: 1rem 0 2rem 0;
        font-size: 3.5rem;
    }
    &__texto {
        margin: 1rem 0;

        &--cantidad {
            margin-top: 4rem;
            font-weight: v.$black_font;
            font-size: 5rem;
            text-align: center;
        }
    }
}