@use 'base/variables' as v;
@use 'base/mixins' as m;

.listado-ponentes {
    @include m.reset-list;
    @include m.grid(1, 1rem);

    &__ponentes {
        color: v.$blue;
        border: 1px solid v.$blue;
        border-radius: 1rem;
        padding: 0.5rem;
        text-align: center;

        &:hover {
            cursor: pointer;
        }

        &--seleccionado {
            @extend .listado-ponentes;
            background-color: v.$blue;
            color: v.$white;
        }
    }
}