  box-sizing: border-box;
  height: 100%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Outfit", sans-serif;
  font-size: 1.8rem;
}

a {
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
}

.swal2-popup {
  font-size: 1.8rem !important;
}

h1, h2, h3 {
  font-family: "Outfit", sans-serif;
  font-weight: #1a1b15;
  margin: 2rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.text-center {
  text-align: center;
}

[class$=__heading] {
  font-family: "Outfit", sans-serif;
  font-weight: #1a1b15;
  margin: 2rem 0;
  margin: 6rem 0 3rem 0;
  text-align: center;
}

[class$=__descripcion] {
  text-align: center;
  font-size: 1.8rem;
  font-weight: 300;
  color: #1E293B;
  margin-bottom: 4rem;
}

.auth {
  width: min(95%, 60rem);
  margin: 0 auto;
}

.auth__text {
  font-size: 2rem;
}

.devtalkus__grid {
  width: min(95%, 120rem);
  margin: 0 auto;
  display: grid;
  gap: 3rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .devtalkus__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}
.devtalkus__texto {
  line-height: 1.5;
}

.paquetes__grid {
  width: min(95%, 120rem);
  margin: 0 auto;
  display: grid;
  gap: 3rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .paquetes__grid {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(3, 1fr);
    align-items: flex-start;
  }
}
.paquetes__submit {
  background-color: #007df4;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  margin-top: 2rem;
}
.paquetes__submit:hover {
  background-color: #0063c1;
  cursor: pointer;
}

.paquete {
  border: 1px solid #7f8ea3;
  padding: 3rem;
}
.paquete__nombre {
  font-weight: 700;
  font-size: 2.5rem;
  margin: 2rem 0;
}
.paquete:nth-child(3) {
  order: -1;
}
@media (min-width: 768px) {
  .paquete:nth-child(3) {
    order: unset;
  }
}
.paquete__lista {
  margin: 0;
  padding: 0;
  list-style: none;
  line-height: 1.5;
}
.paquete__precio {
  font-size: 5.5rem;
  margin: 3rem 0;
  color: #007df4;
  font-weight: 700;
  text-align: center;
}

.agenda {
  padding: 5rem 0;
  width: min(95%, 120rem);
  margin: 0 auto;
}

.eventos__heading {
  color: #007df4;
  margin: 5rem 0;
  text-align: left;
  font-size: clamp(2rem, -0.8rem + 8vw, 5rem);
}
.eventos__fecha {
  color: #64748B;
  margin: 3rem 0 1rem 0;
}
.eventos--workshops .eventos__heading {
  color: #00c8c2;
}
.eventos--workshops .evento__info {
  background-color: #00c8c2;
}
.eventos--workshops .evento__info:hover {
  background-color: #009591;
}
.eventos .swiper-button-next,
.eventos .swiper-button-prev {
  color: #FFFFFF;
}

.evento__hora {
  font-weight: 700;
}
.evento__info {
  padding: 2rem;
  border-radius: 1rem;
  background-color: #007df4;
  color: #FFFFFF;
  transition: 300ms background-color ease;
}
.evento__info:hover {
  background-color: #0063c1;
}
.evento__nombre {
  font-size: 2rem;
  margin: 0;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.evento__introduccion {
  font-size: 1.5rem;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.evento__autor-info {
  display: flex;
  gap: 2rem;
  justify-content: space-between;
  align-items: center;
}
.evento__imagen-autor {
  width: 5rem;
  background-color: #FFFFFF;
  border-radius: 50%;
}
.evento__autor-nombre {
  font-weight: 700;
}
.evento__agregar {
  background-color: #FFFFFF;
  padding: 1.5rem 4rem;
  color: #1a1b15;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  margin-top: 1rem;
}
.evento__agregar:hover {
  background-color: #e6e6e6;
  cursor: pointer;
}
.evento__agregar:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* Resumen */
.resumen {
  background-image: linear-gradient(to right, rgba(0, 125, 244, 0.7), rgba(0, 125, 244, 0.9)), url(../img/bg_numeros.jpg);
  background-image: linear-gradient(to right, rgba(0, 125, 244, 0.7), rgba(0, 125, 244, 0.9)), image-set(url(../img/bg_numeros.avif) 1x, url(../img/bg_numeros.webp) 1x, url(../img/bg_numeros.jpg) 1x);
  padding: 12rem 0;
  background-size: cover;
  background-position: center;
}
.resumen__grid {
  width: min(95%, 120rem);
  margin: 0 auto;
  display: grid;
  gap: 5rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .resumen__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.resumen__bloque {
  text-align: center;
  color: #FFFFFF;
}
.resumen__texto, .resumen__texto--numero {
  font-weight: 900;
  line-height: 1;
  margin: 0;
  font-size: 2.4rem;
  text-transform: uppercase;
}
.resumen__texto--numero {
  font-size: 8rem;
}

/* Speakers */
.speakers__grid {
  width: min(95%, 120rem);
  margin: 0 auto;
  display: grid;
  gap: 5rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .speakers__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1200px) {
  .speakers__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(3, 1fr);
    align-items: flex-start;
  }
}

.speaker {
  border: 1px solid #7f8ea3;
  padding: 3rem;
  background-position: center top;
  background-size: 300px;
  background-repeat: no-repeat;
}
.speaker__nombre {
  font-weight: 700;
  font-size: 2.5rem;
  margin: 2rem 0;
}
.speaker:nth-child(4n+1) {
  background-image: image-set(url("../img/bg_1.avif") 1x, url("../img/bg_1.webp") 1x, url("../img/bg_1.png") 1x);
}
.speaker:nth-child(4n+2) {
  background-image: image-set(url("../img/bg_2.avif") 1x, url("../img/bg_2.webp") 1x, url("../img/bg_2.png") 1x);
}
.speaker:nth-child(4n+3) {
  background-image: image-set(url("../img/bg_3.avif") 1x, url("../img/bg_3.webp") 1x, url("../img/bg_3.png") 1x);
}
.speaker:nth-child(4n+4) {
  background-image: image-set(url("../img/bg_4.avif") 1x, url("../img/bg_4.webp") 1x, url("../img/bg_4.png") 1x);
}
.speaker__imagen {
  min-width: 300px;
}
@media (min-width: 768px) {
  .speaker__imagen {
    min-width: unset;
  }
}
.speaker__ubicacion {
  color: #64748B;
  margin: 1rem 0;
  font-size: 2rem;
  line-height: 1.5;
}
.speaker__listado-skills {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
  margin-top: 1.2rem;
}
.speaker__listado-skills > li {
  background-color: #007df4;
  padding: 1rem;
  border-radius: 0.5rem;
  font-size: 1.4rem;
  font-weight: 700;
  color: #FFFFFF;
}

.speaker-sociales {
  display: flex;
  gap: 2rem;
  margin-top: 1rem;
}
.speaker-sociales__link::before {
  color: #1a1b15;
  font-size: 3rem;
  display: inline-block;
  font-style: normal;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  font-family: "Font Awesome 6 Brands";
}
.speaker-sociales__link[href*="facebook.com"]::before {
  content: "\f39e";
}
.speaker-sociales__link[href*="github.com"]::before {
  content: "\f09b";
}
.speaker-sociales__link[href*="instagram.com"]::before {
  content: "\f16d";
}
.speaker-sociales__link[href*="youtube.com"]::before {
  content: "\f167";
}
.speaker-sociales__link[href*="twitter.com"]::before {
  content: "\f099";
}
.speaker-sociales__hidden {
  display: none;
}

/* Mapa */
.mapa {
  height: 45rem;
}
.mapa__heading {
  margin: 0;
}

/* Boletos */
.boletos__grid {
  width: min(95%, 60rem);
  margin: 0 auto;
  display: grid;
  gap: 5rem;
  grid-template-columns: repeat(1, 1fr);
}

.boleto {
  width: min(100% - 5rem, 65rem);
  margin: 0 auto;
  padding: 6rem;
  border-radius: 2rem;
  color: #FFFFFF;
  position: relative;
}
.boleto::before, .boleto::after {
  position: absolute;
  content: "";
  background-color: #FFFFFF;
  width: 5rem;
  height: 5rem;
  top: calc(50% - 2.5rem);
}
.boleto::before {
  clip-path: circle(50% at 25% 50%);
  left: 0;
}
.boleto::after {
  clip-path: circle(50% at 75% 50%);
  right: 0;
}
.boleto--presencial {
  background-color: hsl(188, 100%, 50%);
  background-image: radial-gradient(at 99% 25%, hsl(209, 100%, 47%) 0px, transparent 50%), radial-gradient(at 78% 25%, hsl(178, 100%, 39%) 0px, transparent 50%), radial-gradient(at 18% 50%, hsl(209, 100%, 47%) 0px, transparent 50%), radial-gradient(at 0% 50%, hsl(178, 100%, 39%) 0px, transparent 50%), radial-gradient(at 80% 50%, hsl(209, 100%, 47%) 0px, transparent 50%), radial-gradient(at 0% 100%, hsl(178, 100%, 39%) 0px, transparent 50%);
}
.boleto--virtual {
  background-color: #00c8c2;
}
.boleto--gratis {
  background-color: #007df4;
}
.boleto__logo {
  color: #FFFFFF;
  text-align: center;
  margin-bottom: 0;
  white-space: nowrap;
  font-size: clamp(2rem, -0.8rem + 10vw, 4rem);
}
@media (min-width: 1024px) {
  .boleto__logo {
    text-align: left;
    margin: 0;
  }
}
.boleto__plan {
  font-size: 1.5rem;
  text-transform: uppercase;
  margin: 2rem 0;
}
.boleto__precio {
  margin: 2rem 0 0 0;
  font-weight: 900;
  font-size: 3rem;
}
.boleto__link-container {
  margin-top: 4rem;
  display: flex;
  justify-content: center;
}
.boleto__link {
  background-color: #007df4;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  width: auto;
}
.boleto__link:hover {
  background-color: #0063c1;
  cursor: pointer;
}
.boleto--acceso {
  width: 25rem;
  height: 40rem;
  padding: 10rem 3rem;
}
@media (min-width: 768px) {
  .boleto--acceso {
    width: 55rem;
    height: 30rem;
    padding: 5rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
}
.boleto--acceso .boleto__logo {
  font-size: clamp(2rem, -0.8rem + 10vw, 3rem);
  margin: 0;
  text-align: left;
}
.boleto--acceso .boleto__nombre {
  font-size: 2.2rem;
  text-transform: uppercase;
  font-weight: 900;
}
.boleto--acceso .boleto__codigo {
  font-size: 3rem;
  font-weight: 900;
  text-align: center;
  position: absolute;
  bottom: 7rem;
  margin: 0;
  left: 0;
  width: 100%;
}
@media (min-width: 768px) {
  .boleto--acceso .boleto__codigo {
    rotate: 90deg;
    transform-origin: 0 0;
    top: 0;
    left: calc(100% - 5rem);
    width: 30rem;
  }
}
.boleto::before {
  clip-path: circle(50% at 50% 25%);
  left: calc(50% - 2.5rem);
  top: 0;
}
@media (min-width: 768px) {
  .boleto::before {
    clip-path: circle(50% at 25% 50%);
    left: 0;
    top: calc(50% - 2.5rem);
  }
}
.boleto::after {
  clip-path: circle(50% at 50% 75%);
  left: calc(50% - 2.5rem);
  top: calc(100% - 5rem);
}
@media (min-width: 768px) {
  .boleto::after {
    clip-path: circle(50% at 75% 50%);
    left: calc(100% - 5rem);
    top: calc(50% - 2.5rem);
  }
}

.pagina_404__container {
  text-align: center;
}
.pagina_404__link {
  color: #007df4;
}

