@use 'base/variables' as v;
@use 'base/mixins' as m;

/* Resumen */
.resumen {
    background-image: linear-gradient(to right, rgba(v.$blue, .7), rgba(v.$blue, .9)), url(../img/bg_numeros.jpg);
    background-image: linear-gradient(to right, rgba(v.$blue, .7), rgba(v.$blue, .9)), image-set( url(../img/bg_numeros.avif) 1x,  url(../img/bg_numeros.webp) 1x, url(../img/bg_numeros.jpg) 1x);
    padding: 12rem 0;
    background-size: cover;
    background-position: center;

    &__grid {
        @include m.container;
        @include m.grid(1, 5rem);
        @include m.tablet{
            @include m.grid(2, 5rem);
        };
    }

    &__bloque {
        text-align: center;
        color: v.$white;
    }

    &__texto {
        font-weight: v.$black_font;
        line-height: 1;
        margin: 0;
        font-size: 2.4rem;
        text-transform: uppercase;

        &--numero {
            @extend .resumen__texto;
            font-size: 8rem;
        }
    }

}

/* Speakers */

.speakers{

    &__grid {
        @include m.container;
        @include m.grid(1, 5rem);
        @include m.tablet {
            @include m.grid(2, 5rem);
        }
        @include m.xl_desktop {
            @include m.grid(3, 5rem);
            align-items: flex-start;
        }
    }
}

.speaker {
    @include m.card;
    background-position: center top;
    background-size: 300px;
    background-repeat: no-repeat;

    @for $i from 1 through 4 {
        &:nth-child(4n + #{$i}) {
            background-image: image-set(
                url('../img/bg_#{$i}.avif') 1x,
                url('../img/bg_#{$i}.webp') 1x,
                url('../img/bg_#{$i}.png') 1x
            );
        }
    }
    &__imagen {
        min-width: 300px;
        @include m.tablet {
            min-width: unset;
        }
    }

    &__ubicacion {
        color: v.$gray;
        margin: 1rem 0;
        font-size: 2rem;
        line-height: 1.5;
    }

    &__listado-skills {
        @include m.tags;
    }
}

.speaker-sociales {
    @include m.social-menu-other(v.$black, 3rem);
    display: flex;
    gap: 2rem;
    margin-top: 1rem;
}

/* Mapa */

.mapa {
    height: 45rem;

    &__heading {
        margin: 0;
    }
}

/* Boletos */

.boletos {
    &__grid {
        @include m.container-sm;
        @include m.grid(1, 5rem);
    }
}

.boleto {
    width: min(100% - 5rem, 65rem );
    margin: 0 auto;
    padding: 6rem;
    border-radius: 2rem;
    color: v.$white;
    position: relative;

    &::before,
    &::after {
        position: absolute;
        content: "";
        background-color: v.$white;
        width: 5rem;
        height: 5rem;
        top: calc(50% - 2.5rem);
    }

    &::before {
        clip-path: circle(50% at 25% 50%);
        left: 0;
    }
    &::after {
        clip-path: circle(50% at 75% 50%);
        right: 0;
    }

    &--presencial {
        background-color:hsla(188,100%,50%,1);
        background-image:
        radial-gradient(at 99% 25%, hsla(209,100%,47%,1) 0px, transparent 50%),
        radial-gradient(at 78% 25%, hsla(178,100%,39%,1) 0px, transparent 50%),
        radial-gradient(at 18% 50%, hsla(209,100%,47%,1) 0px, transparent 50%),
        radial-gradient(at 0% 50%, hsla(178,100%,39%,1) 0px, transparent 50%),
        radial-gradient(at 80% 50%, hsla(209,100%,47%,1) 0px, transparent 50%),
        radial-gradient(at 0% 100%, hsla(178,100%,39%,1) 0px, transparent 50%);
    }

    &--virtual {
        background-color: v.$secondary;
    }

    &--gratis {
        background-color: v.$blue;
    }

    &__logo {
        @include m.logo;
        @include m.fluid-text(2rem, 4rem);
    }
    &__plan {
        font-size: 1.5rem;
        text-transform: uppercase;
        margin: 2rem 0;
    }
    &__precio {
        margin: 2rem 0 0 0;
        font-weight: v.$black_font;
        font-size: 3rem;
    }

    &__link-container {
        margin-top: 4rem;
        display: flex;
        justify-content: center;
    }
    &__link {
        @include m.button(v.$blue, v.$white);
        width: auto;
    }

    $modificador: &;

    &--acceso {
        width: 25rem;
        height: 40rem;
        padding: 10rem 3rem;
        @include m.tablet {
            width: 55rem;
            height: 30rem;
            padding: 5rem;
            display: flex;
            flex-direction: column;
            justify-content: center;
        }

        #{$modificador}__logo {
            @include m.fluid-text(2rem, 3rem);
            margin: 0;
            text-align: left;
        }
        #{$modificador}__nombre {
            font-size: 2.2rem;
            text-transform: uppercase;
            font-weight: v.$black_font;
        }
        #{$modificador}__codigo {
            font-size: 3rem;
            font-weight: v.$black_font;
            text-align: center;
            position: absolute;
            bottom: 7rem;
            margin: 0;
            left: 0;
            width: 100%;
            @include m.tablet {
                rotate: 90deg;
                transform-origin: 0 0;
                top: 0;
                left: calc(100% - 5rem);
                width: 30rem;
            }
        }
    }

    &::before {
        clip-path: circle(50% at 50% 25%);
        left: calc(50% - 2.5rem);
        top: 0;
        @include m.tablet {
            clip-path: circle(50% at 25% 50%);
            left: 0;
            top: calc(50% - 2.5rem);
        }
    }

    &::after {
        clip-path: circle(50% at 50% 75%);
        left: calc(50% - 2.5rem);
        top: calc(100% - 5rem);
        @include m.tablet {
            clip-path: circle(50% at 75% 50%);
            left: calc(100% - 5rem);
            top: calc(50% - 2.5rem);
        }
    }
}