@use 'base/variables' as v;
@use 'base/mixins' as m;

.agenda {
    padding: 5rem 0;
    @include m.container;
}
.eventos {
    $modificador: &;

    &__heading {
        color: v.$blue;
        margin: 5rem 0;
        text-align: left;
        font-size: clamp(2rem, -0.8rem + 8vw, 5rem);
    }
    &__fecha {
        color: v.$gray;
        margin: 3rem 0 1rem 0;
    }

    &--workshops {

        #{$modificador}__heading {
            color: v.$secondary;
        }

        .evento__info {
            background-color: v.$secondary;
            &:hover {
                background-color: darken(v.$secondary, 10%);
            }
        }
    }

    .swiper-button-next,
    .swiper-button-prev { 
        color: v.$white;
    }
}

.evento {
    &__hora {
        font-weight: v.$bold;
    }
    &__info {
        padding: 2rem;
        border-radius: 1rem;
        background-color: v.$blue;
        color: v.$white;
        transition: 300ms background-color ease;
        &:hover {
            background-color: darken(v.$blue, 10%);
        }
    }

    &__nombre {
        font-size: 2rem;
        margin: 0;
        display: -webkit-box;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
        overflow: hidden;
    }

    &__introduccion {
        font-size: 1.5rem;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        overflow: hidden;
    }

    &__autor-info {
        display: flex;
        gap: 2rem;
        justify-content: space-between;
        align-items: center;
    }
    &__imagen-autor {
        width: 5rem;
        background-color: v.$white;
        border-radius: 50%;
    }
    &__autor-nombre {
        font-weight: v.$bold;
    }

    &__agregar {
        @include m.button(v.$white, v.$black);
        margin-top: 1rem;

        &:disabled {
            opacity: .5;
            cursor: not-allowed;
        }
    }
}