@use 'variables' as v;

/** Media Queries **/
@mixin phone {
    @media (min-width: v.$telefono) {
        @content;
    }
}
@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}
@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}
@mixin xl_desktop {
    @media (min-width: v.$xl_desktop) {
        @content;
    }
}

@mixin button($bgColor, $colorTexto) {
    background-color: $bgColor;
    padding: 1.5rem 4rem;
    color: $colorTexto;
    font-size: 1.8rem;
    font-weight: 700;
    border: none;
    transition-property: background-color;
    transition-duration: .3s;
    text-align: center;
    width: 100%;
    display: inline-block;

    &:hover {
        background-color: darken( $bgColor, 10% );
        cursor: pointer;
    }
}

@mixin grid($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}

@mixin container {
    width: min(95%, 120rem);
    margin: 0 auto;
}

@mixin container-md {
    width: min(95%, 80rem);
    margin: 0 auto;
}

@mixin container-sm {
    width: min(95%, 60rem);
    margin: 0 auto;
}

@mixin headings {
    font-family: v.$main_font;
    font-weight: v.$black;
    margin: 2rem 0;
}

@mixin page-name {
    @include headings;
    margin: 6rem 0 3rem 0;
    text-align: center;
}
@mixin descripcion-pagina {
    text-align: center;
    font-size: 1.8rem;
    font-weight: v.$light;
    color: v.$dark-gray;
    margin-bottom: 4rem;
}

@mixin fluid-text($min, $max) {
    font-size: clamp($min, -0.8rem + 10vw ,$max);
}

@mixin gradient-text($start, $end) {
    background: linear-gradient(to right, $start, $end);
    background-clip: text;
    color: transparent;
}

@mixin logo {
    color: v.$white;
    text-align: center;
    margin-bottom: 0;
    white-space: nowrap;
    @include desktop {
        text-align: left;
        margin: 0;
    }
}

@mixin social-menu-other($color, $size) {

    &__link {

        &::before {
            color: $color;
            font-size: $size;
            display: inline-block;
            font-style: normal;
            text-rendering: auto;
            -webkit-font-smoothing: antialiased;
            font-family: "Font Awesome 6 Brands";
        }

        &[href*="facebook.com"]::before {
            content: "\f39e";
        }
        &[href*="github.com"]::before {
            content: "\f09b";
        }
        &[href*="instagram.com"]::before {
            content: "\f16d";
        }
        &[href*="youtube.com"]::before {
            content: "\f167";
        }
        &[href*="twitter.com"]::before {
            content: "\f099";
        }
    }

    &__hidden {
        display: none;
    }
}

@mixin link {
    color: v.$white;
    font-size: 1.4rem;
    text-transform: uppercase;
    font-weight: v.$bold;
    background-color: transparent;
    border: none;

    &:hover {
        color: v.$blue;
        cursor: pointer;
    }
}

@mixin shadow {
    box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.1 ) 0px 4px 12px;
}

@mixin reset-list {
    margin: 0;
    padding: 0;
    list-style: none;
}

@mixin tags {
    @include reset-list;
    display: flex;
    flex-wrap: wrap;
    gap: 1.5rem;
    margin-top: 1.2rem;

    > li {
        background-color: v.$blue;
        padding: 1rem;
        border-radius: .5rem;
        font-size: 1.4rem;
        font-weight: v.$bold;
        color: v.$white;
    }
}

@mixin card {
    border: 1px solid lighten(v.$gray, 10);
    padding: 3rem;

    &__nombre {
        font-weight: v.$bold;
        font-size: 2.5rem;
        margin: 2rem 0;
    }
}

@mixin bg-gradient($bg_color) {
    background-image: linear-gradient(to left top, $bg_color, lighten($bg_color, 15%));
}